@import "../abstracts/_text-styles";
@import "../abstracts/_color-styles";
@import "../components/button";

.feed-friend {
  display: flex;
  width: 100%;
  margin: 0 auto;
  background: $gradient-feed-friend;
  opacity: 0.9;
  padding-bottom: 50px;
}

.container-feed {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 72.5%;
  background: rgba(253, 253, 255, 0.52);
  backdrop-filter: blur(2px);
  border-radius: 20px;
  margin: 69px auto 0;
  padding-bottom: 50px;
  h2 {
    @include h2;
    color: $color-orange-light;
    margin-bottom: 0;
  }
  .text {
    padding-left: 40px;
    color: $color-black-blue;
    h3 {
      @include h3;
      margin: 33px 0;
    }
    .medium {
      @include medium;
      padding-top: 12px;
    }
    p {
      @include p;
      margin-top: -6px;
    }
    .p-link {
      margin-top: -16px;
    }
    span {
      border-bottom: solid 1px;
      border-bottom-color: $color-orange-light;
      padding-bottom: 1px;
      a {
        color: $color-black-blue;
      }
    }
  }
  h3 {
    @include h3-36;
    color: $color-black-blue;
    margin-top: 45px;
  }
  .feed-cards {
    display: flex;
    justify-content: space-between;
    width: 764px;
    padding-top: 1px;
    .feed-card {
      width: 168px;
      height: 168px;
      border: solid 1px;
      border-radius: 5px;
      border-color: $color-orange-light;
      background-color: $color-white;
      background-repeat: no-repeat;
      background-position: center;
    }
    p {
      padding-top: 14px;
      @include feed-p;
      color: $color-black-blue;
    }
    .one {
      width: 168px;
      &::after {
        content: url(../assets/svg/arrow.svg);
        position: relative;
        left: 197px;
        bottom: 177px;
      }
      .feed-card {
        background-image: url(../assets/svg/pay.svg);
      }
    }
    .two {
      width: 168px;
      &::after {
        content: url(../assets/svg/arrow.svg);
        position: relative;
        left: 197px;
        bottom: 204px;
      }
      .feed-card {
        background-image: url(../assets/svg/zoo.svg);
      }
    }
    .three {
      width: 168px;
      .feed-card {
        background-image: url(../assets/svg/monkey.svg);
      }
    }
  }
  button {
    width: 298px;
    height: 54px;
    margin-top: 61px;
    padding-right: 2px;
  }
}

@media (max-width: 1400px) {
  .container-feed {
    width: 94%;
    margin: 30px auto 0;
    h2 {
      margin-top: 30px;
    }
    .text {
      padding-left: 1px;
      h3 {
        margin: 21px 0 15px;
      }
      .medium {
        max-width: 800px;
      }
      p {
        margin-top: -7px;
        max-width: 800px;
      }
      .p-link {
        position: relative;
        bottom: 22px;
        left: 65px;
      }
    }
    h3 {
      margin-top: 1px;
    }
    .feed-cards {
      padding-top: 4px;
    }
    button {
      margin-top: 12px;
    }
  }
}

@media (max-width: 950px) {
  .feed-friend {
    padding-bottom: 20px;
  }
  .container-feed {
    width: 100%;
    margin: 0 auto;
    padding-top: 20px;
    padding-bottom: 60px;
    h2 {
      margin-top: 12px;
      font-size: 42px;
    }
    .text {
      padding-left: 20px;
      h3 {
        margin: 21px 0 15px;
        font-size: 22px;
      }
      .medium {
        max-width: 600px;
        padding-top: 8px;
      }
      p {
        margin-top: -7px;
      }
      .p-600 {
        max-width: 600px;
      }
      .p-link {
        max-width: 280px;
        bottom: 21px;
        left: 270px;
      }
    }
    h3 {
      margin-top: -6px;
      margin-bottom: 14px;
    }
    .feed-cards {
      width: 92%;
      padding-top: 4px;
      padding-right: 14px;
      margin: 0 auto;

      .one {
        &::after {
          content: url(../assets/svg/arrow-green.svg);
          position: relative;
          left: 137px;
          bottom: 253px;
        }
      }
      .two {
        &::after {
          content: url(../assets/svg/arrow-green.svg);
          position: relative;
          left: 133px;
          bottom: 277px;
        }
      }
    }
    button {
      margin-top: -9px;
    }
  }
}

@media (max-width: 610px) {
  .feed-friend {
    padding-bottom: 20px;
  }
  .container-feed {
    width: 94%;
    margin: 20px auto 0;
    padding-top: 0px;
    padding-bottom: 40px;
    h2 {
      margin-top: 23px;
      font-size: 24px;
    }
    .text {
      text-align: center;
      padding-left: 0;
      h3 {
        margin: 9px 0 24px;
        font-size: 18px;
      }
      .medium,
      .p-2 {
        display: none;
      }
    }
    h3 {
      font-size: 24px;
      margin-top: -6px;
      margin-bottom: 18px;
    }
    .feed-cards {
      width: 94%;
      height: auto;
      flex-direction: column;
      align-items: center;
      padding-top: 0;
      padding-right: 0;
      p {
        padding-top: 0;
        margin: 11px 0 9px;
      }
      .one {
        height: 243px;
        &::after {
          content: url(../assets/images/arrow-rotate.png);
          position: relative;
          left: 174px;
          bottom: 47px;
        }
      }
      .two {
        height: 250px;
        &::after {
          content: url(../assets/images/arrow-rotate.png);
          position: relative;
          left: 174px;
          bottom: 64px;
        }
      }
    }
    button {
      margin-top: 29px;
    }
  }
}
