@import "../abstracts/text-styles";
@import "../abstracts/color-styles";
@import "../components/nav-hamburger";
@import "../components/button";
@import "../components/input-radio";
@import "../components/input-range";

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
.panda-container {
  width: 100%;
  background-color: $color-black-blue;
}
.panda {
  height: 386px;
  margin: 0 auto;
  background-image: url(../assets/images/panda-donate-desktop.jpg);
  background-repeat: no-repeat;
  background-position: center;
}
.donate {
  padding-top: 11px;
  padding-bottom: 119px;
  background: $gradient-feed-friend;
  .donate-text {
    width: 72.5%;
    height: 238px;
    margin: 0 auto;
    h2 {
      @include h3-36;
      color: $color-white;
      margin-bottom: -7px;
    }
    .p-donate-text {
      margin-top: 16px;
      @include medium;
      color: $color-white;
    }
    h3 {
      @include h3-36;
      color: $color-orange-light;
      margin: 46px 0 0;
    }
  }
  .donate-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 61%;
    margin: 59px auto;
    background: rgba(255, 255, 255, 0.52);
    backdrop-filter: blur(2px);
    border-radius: 20px;
    padding: 40px 92px 48px;
    .h2-donate-main {
      @include h2;
      color: $color-orange-light;
      margin: 0 14px 0 0;
    }
    .h3-donate-main {
      width: 100%;
    }
    h3 {
      @include h3;
      color: $color-black-blue;
      margin-top: 22px;
    }
    .panda-diet {
      display: flex;
      justify-content: space-between;
      width: 550px;
      height: 236px;
      margin-top: 35px;
      .amount-days {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        &::after {
          position: relative;
          left: 172px;
          bottom: 92px;
          width: 14px;
          height: 14px;
          content: url(../assets/svg/x-icon.svg);
        }
      }
      h2 {
        @include number;
        color: $color-black;
        text-align: center;
        margin: 0px 0 -6px 4px;
      }
      .p-panda-diet {
        @include feed-p;
        color: $color-black-blue;
        margin-bottom: 25px;
        padding-left: 2px;
      }
    }
    .panda-image {
      width: 284px;
      height: 246px;
      background-image: url(../assets/svg/panda-widget.svg);
      margin-right: 16px;
    }
    .share {
      @include feed-p;
      color: $color-black-blue;
      margin-top: 30px;
      margin-left: 5px;
    }
  }
  div .amount {
    width: 100%;
    margin-top: 30px;
    padding-left: 2px;
    p .amount {
      @include feed_p;
      color: $color-black;
      margin-bottom: 5px;
    }
  }
  .donate-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .range-donate {
    position: relative;
    -webkit-appearance: none;
    appearance: none;
    width: 97.5%;
    height: 27px;
    outline: none;
    -webkit-transition: 0.2s;
    transition: opacity 0.2s;
    border-left: solid 11px #404950;
    border-right: solid 14px #404950;
    background-color: #404950;
    border-radius: 2px;
    margin: 3px 0 13px;
    &:hover {
      cursor: pointer;
    }
  }
  & ::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    position: relative;
    z-index: 3;
    width: 40px;
    height: 40px;
    background-image: url(../assets/svg/roller-icon.svg);
  }
  & ::-webkit-slider-runnable-track {
    height: 42px;
  }

  .number {
    width: 260px;
    height: 50px;
    border: 1px solid #929699;
    border-radius: 2px;
    margin-top: 47px;
    background-color: transparent;
    color: #929699;
    @include feed_p;
    &:focus:required:invalid {
      border: 1px solid $color-red;
      color: $color-red;
      &::placeholder {
        color: $color-red;
      }
    }
    &:focus:required:invalid ~ .radio::after {
      filter: invert(26%) sepia(78%) saturate(3388%) hue-rotate(348deg) brightness(80%) contrast(108%);
    }
    &:focus:required {
      border: 1px solid $color-green;
      color: $color-green;
    }
    &:focus:required:valid ~ .radio::after {
      filter: invert(41%) sepia(47%) saturate(4977%) hue-rotate(64deg) brightness(101%) contrast(101%);
    }
  }
  .radio {
    display: flex;
    justify-content: space-between;
    width: 250px;
    @include feed-p;
    color: $color-black-blue;
    margin-top: 25px;
    .monthly,
    .once {
      -webkit-appearance: none;
      appearance: none;
      font: inherit;
      color: $color-orange-light;
      width: 16px;
      height: 16px;
      border: 1px solid $color-orange-light;
      border-radius: 50%;
      cursor: pointer;
    }
    .monthly {
      transform: translate(19px, 2px);
    }
    .once {
      transform: translate(20px, 2px);
    }
    .label-monthly {
      margin-left: -1px;
    }
    .label-once {
      margin-right: -7px;
    }
    &::after {
      position: relative;
      bottom: 62px;
      left: 0;
      width: 10px;
      height: 20px;
      content: url(../assets/svg/dollar.svg);
    }
  }
}

#tickmarks {
  display: flex;
  justify-content: space-between;
  width: 100%;
  @include dollar;
  option {
    padding-left: 0;
    width: 2.6em;
    cursor: pointer;
    &.black {
      color: $color-black;
    }
    &.yellow {
      color: $color-orange-light;
    }
    //     &::before {
    //         content: url(../assets/svg/dollar.svg);
    //         filter: invert(0%) sepia(0%) saturate(3%) hue-rotate(164deg) brightness(20%) contrast(100%);
    //     }
  }
  & option:nth-child(1) {
    display: block;
  }
}

@media (max-width: 1400px) {
  .panda {
    height: 390px;
    margin: 0 auto 0 -17%;
    background-repeat: no-repeat;
    background-position: left;
  }
  .donate {
    padding-top: 11px;
    padding-bottom: 29px;
    .donate-text {
      width: 94%;
      margin: 0 auto;
      h2 {
        padding-right: 10%;
      }
      .p-donate-text {
        margin-top: 36px;
      }
      h3 {
        margin: 46px 0 0;
      }
    }
    .donate-main {
      width: 85%;
      margin: 106px auto 0;
      padding: 20px 47px 30px;
      .h2-donate-main {
        margin: 0 14px 0 0;
      }
      h3 {
        margin-top: 13px;
      }
    }
    div .amount {
      padding-left: 13px;
    }
    .range-donate {
      width: 95.5%;
      border-left: solid 0px #404950;
      border-right: solid 0px #404950;
    }
  }

  #tickmarks {
    width: 96%;
    option {
      width: 1.3em;
    }
    & option:nth-child(1) {
      display: none;
    }
    & option:nth-child(3) {
      color: $color-orange-light;
    }
  }
}

@media (max-width: 950px) {
  .panda {
    height: 335px;
    margin: 34px auto 0;
    background-image: url(../assets/images/panda-donate-tablet.jpg);
    background-repeat: no-repeat;
    background-position: center;
  }
  .donate {
    height: auto;
    padding-top: 0;
    padding-bottom: 46px;
    .donate-text {
      width: auto;
      height: auto;
      margin: 0 auto;
      padding-left: 20px;
      h2 {
        font-size: 24px;
        padding-right: 7%;
        margin-top: 0;
        padding-top: 29px;
      }
      .p-donate-text {
        font-size: 16px;
        margin-top: 24px;
        padding-right: 15px;
        line-height: 138%;
      }
      h3 {
        font-size: 36px;
        margin: 30px 0 0;
      }
    }
    .donate-main {
      width: 94%;
      height: auto;
      margin: 29px auto 0;
      padding: 30px 0 15px;
      .h2-donate-main {
        margin: 0;
      }
      .h3-donate-main {
        width: 600px;
      }
      h3 {
        text-align: center;
        margin-top: 22px;
      }
      .panda-diet {
        margin-top: 25px;
      }
      .share {
        margin-top: 29px;
        margin-left: 3px;
      }
    }
    div.amount {
      margin-top: 21px;
      padding-left: 0;
      p {
        padding-left: 10px;
        margin-top: 32px;
        margin-bottom: 6px;
      }
    }
    .range-donate {
      width: 93%;
      border-left: solid 20px #404950;
      border-right: solid 20px #404950;
    }
    .number {
      width: 170px;
      height: 38px;
    }
    .radio {
      width: 257px;
      margin-top: 32px;
      &::after {
        left: -48px;
      }
      .label-once {
        margin-right: -2px;
      }
    }
    .button {
      margin-top: 29px;
      margin-left: 4px;
    }
  }

  #tickmarks {
    width: 95%;
    option {
      width: 50px;
    }
    & option:nth-child(2),
    & option:nth-child(3) {
      display: none;
    }
    & option:nth-child(6) {
      color: $color-orange-light;
    }
    & option:nth-child(5) {
      color: $color-black;
    }
  }
}

@media (max-width: 610px) {
  .panda {
    height: 187px;
    margin: 34px auto 0;
    background-image: url(../assets/images/panda-donate-m.jpg);
    background-repeat: no-repeat;
    background-position: center;
  }
  .donate {
    padding-top: 0;
    padding-bottom: 22px;
    .donate-text {
      padding-left: 10px;
      h2 {
        line-height: 121%;
      }
      .p-donate-text {
        font-size: 16px;
        margin-top: 28px;
        padding-right: 12px;
        line-height: 138%;
      }
      h3 {
        font-size: 24px;
        margin: 28px 0 0;
      }
    }
    .donate-main {
      width: 94%;
      height: auto;
      margin: 29px auto 0;
      padding: 21px 0 0;
      .h2-donate-main {
        font-size: 24px;
        margin: 0;
      }
      .h3-donate-main {
        width: 300px;
      }
      h3 {
        text-align: center;
        font-size: 16px;
        margin-top: 12px;
      }
      .panda-diet {
        width: 300px;
        height: auto;
        margin-top: 25px;
        .amount-days {
          justify-content: center;
          &::after {
            left: 93px;
            bottom: 63px;
            width: 8px;
            height: 8px;
          }
        }
      }

      .amount-days h2 {
        font-size: 40px;
        margin: 30px 0 0px 0px;
      }
      .amount-days .p-panda-diet {
        font-size: 9px;
        margin-bottom: 20px;
        padding-left: 0px;
      }
      .panda-image {
        width: 160px;
        height: 112px;
        background-size: 100%;
        background-position-y: -29px;
        background-repeat: no-repeat;
        margin-right: 0;
      }
      .share {
        margin-top: 30px;
        margin-left: -1px;
        margin-bottom: 19px;
      }
    }
    div.amount {
      margin-top: 0;
      padding-left: 0;
      p {
        padding-left: 0;
        margin-top: 4px;
        margin-bottom: 6px;
      }
    }
    .range-donate {
      height: 13px;
      margin: 5px 0 0;
      border-left: solid 10px #404950;
      border-right: solid 9px #404950;
    }
    & ::-webkit-slider-thumb {
      width: 20px;
      height: 20px;
      background-image: url(../assets/svg/roller-icon.svg);
      background-size: 100%;
    }
    & ::-webkit-slider-runnable-track {
      height: 29px;
    }
    .number {
      padding-left: 4px;
      margin-top: 12px;
    }
    .radio {
      width: 257px;
      margin-top: 25px;
      &::after {
        left: -47px;
        bottom: 56px;
      }
      .label-once {
        margin-right: 0;
      }
    }
    .button {
      margin-top: 27px;
      margin-left: 0px;
      padding-right: 2px;
    }
  }

  #tickmarks {
    width: 95%;
    option {
      font-size: 13px;
      width: 26px;
    }
    & option:nth-child(2),
    & option:nth-child(3) {
      display: none;
    }
  }
}
